@import url(https://fonts.googleapis.com/css?family=Shadows+Into+Light+Two&display=swap);
:root {
  --activity-0: #5ec7f8; /* swimming */
  --activity-1: #f78c6c; /* yoga */
  --activity-2: #9bda7e; /* gym */
  --activity-3: #ffcb6b; /* biking */
  --activity-4: #bb80b3; /* running */
  --grey: #caccd6;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: black;
}

.App {
  font-family: "Shadows Into Light Two", cursive;
}
.wrapper {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}
.title {
  font-size: 24px;
  padding: 0 16px 4px;
  text-align: center;
  margin-top: 24px;
}
.Legend {
  margin-top: 8px;
}
.Main,
.ByDay,
.ByMonth,
.Footer {
  margin-top: 32px !important;
  border-top: 1px solid var(--grey);
  padding-top: 32px;
}
.Footer {
  margin-bottom: 32px;
}

.bg-0 {
  background-color: var(--activity-0);
}
.bg-1 {
  background-color: var(--activity-1);
}
.bg-2 {
  background-color: var(--activity-2);
}
.bg-3 {
  background-color: var(--activity-3);
}
.bg-4 {
  background-color: var(--activity-4);
}
.fill-0 {
  fill: var(--activity-0);
}
.fill-1 {
  fill: var(--activity-1);
}
.fill-2 {
  fill: var(--activity-2);
}
.fill-3 {
  fill: var(--activity-3);
}
.fill-4 {
  fill: var(--activity-4);
}
.fill-black {
  fill: black;
}

.types {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.item,
.type {
  display: flex;
  align-items: center;
}
.item .type {
  text-transform: capitalize;
}
.item .type .icon {
  width: 12px;
  height: 12px;
  border-radius: 6px;
  margin-right: 4px;
}
.item .count {
  margin-left: 8px;
}
.item:not(:last-child) {
  margin-right: 16px;
}

.Main {
  border-spacing: 0px;
}
.row-day {
  font-size: 12px;
}
.month-name {
  text-align: right;
  padding-right: 8px;
  margin-left: 12px;
}
.week-label {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 24px;
  overflow: hidden;
}
.month-dates {
  display: inline-flex;
  flex-wrap: wrap;
}
.month-week {
  display: inline-flex;
}
.day,
.date {
  width: 32px;
  border: 2px solid transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}
.date {
  height: 32px;
  position: relative;
}
.date.valid,
.date.black {
  cursor: pointer;
}
.date.black.valid.with-data {
  color: white;
}
.active {
  position: absolute;
  top: 0;
  left: 0;
}
.inactive {
  position: absolute;
  top: 6px;
  left: 6px;
}
.active .fill,
.active .fill-half {
  position: absolute;
  width: 32px;
  height: 32px;
}
.active .fill {
  z-index: -2;
}
.active .fill-half {
  z-index: -1;
}
.inactive .fill {
  position: absolute;
  width: 24px;
  height: 24px;
}

.tooltip {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  -webkit-transform: translate(calc(-50% + 16px), calc(-100% + 4px));
          transform: translate(calc(-50% + 16px), calc(-100% + 4px));
}

.tooltip-content {
  background-color: black;
  color: white;
  font-size: 14px;
  padding: 8px 12px;
  border-radius: 4px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.tooltip-activity {
  margin-top: 4px;
}

.tooltip-type {
  text-transform: capitalize;
  display: inline-block;
  color: black;
  padding: 0 6px;
  border-radius: 2px;
}
.tooltip-arrow {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid black;
  margin-left: calc(50% - 16px + 5px);
}

.Bars {
  width: 160px;
}
.bar-wrapper {
  width: 100%;
  position: relative;
}
.bar {
  height: 20px;
}
.value {
  margin-left: 6px;
}
.value.inside {
  position: absolute;
  right: 6px;
}

.ByDay {
  display: flex;
}

.labels {
  margin-top: 32px; /* the handwriting font has a bit larger top margin, so reduce 2px */
  margin-right: 16px;
  margin-left: 12px;
}
.days {
  display: flex;
  flex-wrap: wrap;
}
.day-wrapper:not(:last-child) {
  margin-right: 20px;
  margin-bottom: 12px;
}

.day-title {
  height: 32px;
}

.row {
  height: 28px;
  display: flex;
  align-items: center;
}
.row.label {
  justify-content: flex-end;
  text-transform: capitalize;
}
.row.total {
  margin-top: 8px;
  position: relative;
}
.row.total .text,
.row.total .number {
  border-top: 1px solid black;
  padding-top: 4px;
  display: inline-block;
}
.row.total .text {
  padding-left: 16px;
}
.row.total .number {
  padding-right: 16px;
}

.ByMonth {
  border-spacing: 8px;
  width: 100%;
  margin: 0 auto;
  max-width: 600px;
  padding-right: 20px;
}
.stacked {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
}
.stack {
  height: 24px;
  font-size: 12px;
  text-align: right;
  box-sizing: border-box;
  padding-top: 4px;
  padding-right: 4px;
}
.ByMonth .label {
  text-align: right;
  width: 20px;
}
.ByMonth .total {
  margin-left: 6px;
  position: absolute;
}

